@import url("https://fonts.googleapis.com/css2?family=Nanum+Gothic+Coding&display=swap");

::-webkit-scrollbar {
  width: 0.35rem; 
} 

::-webkit-scrollbar-thumb { 
 background-color: #d6d6d6;
} 

::-webkit-scrollbar-track { 
 background-color: rgb(255, 255, 255);
}

#post-modal-background {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 10000;
  padding: 0;
  background-color: rgba(0, 0, 0, 0.5);
  transition: 0.5s linear;
}

#post-modal-container {
  display: flex;
  flex-direction: row;
  align-items: center;
  transform: translate(-50%, -50%);
  z-index: 10000;
  top: 50%;
  left: 50%;
  width: 65rem;
  height: 95%;
  max-height: 95%;
  max-width: 80%;
  position: absolute;
  background-color: white;
  border: none;
}

#post-container {
  flex: 1;
  position: relative;
  max-width: 100%;
  max-height: 100%;
  width: 50rem;
  height: 100%;
  display: flex;
  flex-direction: column;
  padding: 1rem 0 0 0;
}

#post-container-1 {
  height: 2.5rem;
  position: relative;
  display: flex;
  flex-direction: row-reverse;
  margin: 0 1rem 0 1rem;
  max-width: 100%;
}

.post-1-wrap {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

#post-container-2 {
  display: flex;
  flex-direction: column;
  height: 6rem;
  max-width: 100%;
  margin: 0 1rem 0.8rem 1rem;
  padding: 0 1rem 1rem 1rem;
  /* border-bottom: solid lightgray 1px; */
}

#post-2-wrap {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}

#post-2-wrap2 {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}

#post-container-3 {
  background-color: rgb(242, 242, 242);
  overflow-y: auto;
  position: relative;
  max-width: 100%;
  /* max-height: 35rem; */
  flex: 100%;
  margin-bottom: 0.8rem;
  border-top: dotted lightgray 1px;
  border-bottom: dotted lightgray 1px;
}

.post-1-content {
  display: flex;
  align-items: center;
  justify-content: center;
  height: inherit;
  min-width: 3rem;
  min-height: 3rem;
  width: fit-content;
  margin: 0 0.5rem 0 0.5rem;
  border-radius: 70%;
  border: 1px dotted lightgray;
}

#post-2-header {
  display: flex;
  flex-direction: row;
  justify-content: left;
}

.post-2-content {
  width: fit-content;
  max-width: 100%;
}

.post-2-content-width-100 {
  width: 100%;
  max-width: 100%;
}

.post-icon-btn-config {
  padding: 0 0 1rem 0.16rem;
}

.post-1-content-icon-container {
  display: flex;
  align-items: center;
  justify-content: center;
  padding-bottom: 0.4rem;
}

.post-1-content-icon {
  transform: scale(1.8);
}

/* .post-1-content-icon-hover:hover{
  cursor: pointer;
  transform: scale(2.2);
} */

.post-like-box,
.post-like-box-mobile {
  display: flex;
  height: fit-content;
}

.post-like-img {
  /* position: relative; */
  padding: 0;
  color: rgb(255, 63, 63);
  transform: scale(1.5);
}

.post-unlike-img {
  /* position: relative; */
  padding: 0;
  color: rgb(174, 174, 174);
  transform: scale(1.5);
}

.post-like {
  /* position: relative; */
  margin-left: 0.5rem;
  font-size: 1.5rem;
  text-align: left;
}

#post-profile-img {
  /* border-radius: 70%; */
  height: 3rem;
  width: 3rem;
  border-radius: 70%;
  object-fit: cver;
  /* margin: 0 0.5rem 0 0.5rem; */
  /* border: lightgray dotted 1px; */
}

i.close.icon.post-1-content-icon,
i.trash.alternate.outline.icon.post-1-content-icon,
i.mail.outline.icon.post-1-content-icon,
i.edit.outline.icon.post-1-content-icon,
i.like.icon.post-like-img,
i.like.icon.post-unlike-img {
  margin: 0;
}

#post-container-3::-webkit-scrollbar {
  width: 0.4rem;
  background-color: white;
}

.icon-like-unlike-img {
  color: rgb(174, 174, 174);
}

.icon-like-like-img {
  color: lightcoral;
}

.post-name {
  font-size: 2rem;
  padding: 1rem 0rem 1rem 0;
  margin-bottom: 1rem;
}

.post-genre {
  position: relative;
  /* flex: 1; */
  order: 1;
  font-size: 1.1rem;
  padding-right: 1rem;
  text-align: left;
}

.post-desc {
  position: relative;
  /* flex: 1; */
  order: 2;
  font-size: 1.1rem;
  padding-left: 1rem;
  text-align: left;
  border-left: solid lightgray 2px;
}

.post-btn-hover:hover,
.post-like-box:hover {
  cursor: pointer;
  transform: scale(1.2);
}

.post-media-data {
  display: flex;
  justify-content: center;
  max-width: 100%;
  align-items: center;
  max-height: 40rem;
}

.post-media-data2 {
  display: flex;
  justify-content: center;
  max-width: 100%;
  align-items: center;
  max-height: 40rem;
}

.ui.centered.fluid.card.post-media-data {
  max-width: 100%;
  min-height: 40rem;
  margin: 0rem 0 1rem 0;
}

div.ui.centered.fluid.card.post-media-data2 {
  max-width: 100%;
  min-height: 40rem;
  margin: 0rem 0 1rem 0;
}

.post-div-img-vertical-center{
  position: relative;
  display: flex;
  flex-direction: column;
  max-height: 100%;
  height: 100%;
  max-width: 100%;
  justify-content: center;
  align-items: center;
}

.post-div-img {
  overflow-y: auto;
  width: 99%;
  border-radius: 10px;
  height: fit-content;
  padding: 0.8rem 0.8rem 0 0.8rem;
  /* margin: 0rem 0rem 0rem 0.8rem; */
}

.post-div-img2 {
  overflow-y: auto;
  width: 99%;
  border-radius: 10px;
  height: 100%;
  padding: 0.8rem 0.8rem 0rem 0.8rem;
  /* margin: 0rem 0rem 0rem 0.8rem; */
}

.post-div-img::-webkit-scrollbar,
.post-div-img2::-webkit-scrollbar {
  width: 0.4rem;
}

.post-edit-upload-container {
  width: 100%;
  max-width: 100%;
}


.post-div-img2 > section > form > section {
  margin: 0 0 1rem 0;
}


.post-div-img2 > section > div > div > aside > i {
  margin: 0;
  border: solid rgba(255, 255, 255, 0.3) 5px;
  background-color: rgba(255, 255, 255, 0.3);
  border-radius: 70%;
}

.post-div-img2 > section > div > div > aside {
  display: flex;
  flex-direction: row-reverse;
  margin: 1rem 1rem 0 0;
}

.post-media-data img {
  /* margin: 2rem 0 2rem 0; */
  max-width: 100%;
  max-height: 40rem;
  object-fit: contain;
}

.post-media-data2 img {
  /* margin: 2rem 0 2rem 0; */
  max-width: 100%;
  max-height: 40rem;
  object-fit: contain;
}

.post-media-data-mobile,
.post-media-data video {
  /* margin: 0rem 0 2rem 0; */
  /* max-width: 100%;
  max-height: 40rem; */
  object-fit: contain;
}

.post-video {
  width: 100%;
  max-width: 100%;
  max-height: 40rem;
}

/* .desc {
  margin: 0px 0px 20px 13px;
} */

.post-image {
  text-align: center;
}

/* MOBILE */
#post-modal-container-mobile {
  display: flex;
  flex-direction: row;
  align-items: center;
  transform: translate(-50%, -50%);
  z-index: 10000;
  top: 50%;
  left: 50%;
  width: 100%;
  height: 100%;
  max-height: 100%;
  max-width: 100%;
  position: absolute;
  background-color: white;
  border: none;
}

#post-container-mobile {
  flex: 1;
  position: relative;
  max-width: 100%;
  max-height: 100%;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  padding: 1rem 0 0 0;
}

#post-container-1-mobile {
  height: 3rem;
  position: relative;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: 0 1rem 0 1rem;
  max-width: 100%;
}

.post-no-padding {
  padding: 0;
}

#post-container-2-mobile {
  display: flex;
  flex-direction: column;
  height: 6rem;
  max-width: 100%;
  margin: 1rem 1rem 1rem 1rem;
  /* border-bottom: dotted rgb(104, 104, 104) 2px; */
}

i.dropdown.icon {
  transform: scale(1.3);
}

div.menu.transition.visible {
  margin: 0.5rem 1rem 0 -0.19rem;
  padding: 0.5rem 1rem 0 0;
}

.ui.centered.fluid.card.post-media-data-mobile {
  min-height: 24.5rem;
  max-height: 24.5rem;
  max-width: 100%;
  margin: 0rem 0 1rem 0;
}

.post-media-data-mobile {
  display: flex;
  justify-content: center;
  max-width: 100%;
  align-items: center;
  max-height: 24.5rem;
}

.post-media-data-mobile img {
  /* margin: 2rem 0 2rem 0; */
  max-width: 100%;
  max-height: 100%;
  min-height: 100%;
}

.post-media-data-mobile video {
  /* margin: 0rem 0 2rem 0; */
  max-width: 100%;
  max-height: 100%;
  min-height: 100%;
}

.post-video-mobile {
  width: 100%;
  max-width: 100%;
  max-height: 24.5rem;
}

/* .desc {
  margin: 0px 0px 20px 13px;
} */

.post-image-mobile {
  text-align: center;
}
/* 
span.ant-input-group.ant-input-group-compact {
  width: 20rem;
} */

div.menu.transition.visible.post-2-genre-dropdown {
  margin-left: -1rem;
}

.post-edit-desc { 
  margin-left: 1rem;
  width: 13rem;
  font-size: 1rem;
}


.post-edit-upload-container-mobile > form > section > article {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

.post-edit-preview-container-mobile {
  align-items: center;
  justify-content: center;
  max-width: 100%
}

.post-edit-upload-container-mobile > form > section > article > section > section  {
  width: fit-content;
  max-width: 100%;
  margin: 0;
}

.post-edit-upload-container-mobile > form > section > article > section > section > div > div > aside > i {
  margin-right: 2.5rem;
}


.post-edit-upload-container-mobile > form > section > article > section > section > div > img {
  width: fit-content;
  max-width: 8rem;
}

.post-edit-upload-container-mobile > form > section > article > section > section > div {
  width: fit-content;
  max-width: 8rem;
}

