[data-trigger] {
  opacity: 0;
  position: relative;
  top: 10rem;
  transition: 0.5s ease;
}

[data-trigger].visible {
  opacity: 1;
  top: 0rem;
  /* transition-duration: 0.5s;
  opacity: 0; */
  /* transition-duration: 0.5s; */
  /* transition-delay: 1s; */
}

/* 다시 사라지는 이벤트 넣기 */

[feedback-trigger] {
  opacity: 0;
  position: relative;
  right: 10rem;
  transition: 0.8s ease-out;
}

[feedback-trigger].visible {
  opacity: 1;
  right: 0;
}

