/* css loading */
.cssload-spin-box {
	position: absolute;
	margin: auto;
	/* left: 0;
	top: 0;
	bottom: 0;
	right: 0; */
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
	width: 23px;
	height: 23px;
	border-radius: 100%;
	box-shadow: 23px 23px rgb(79,77,73), -23px 23px rgb(223,223,223), -23px -23px rgb(79,77,73), 23px -23px rgb(223,223,223);
		-o-box-shadow: 23px 23px rgb(79,77,73), -23px 23px rgb(223,223,223), -23px -23px rgb(79,77,73), 23px -23px rgb(223,223,223);
		-ms-box-shadow: 23px 23px rgb(79,77,73), -23px 23px rgb(223,223,223), -23px -23px rgb(79,77,73), 23px -23px rgb(223,223,223);
		-webkit-box-shadow: 23px 23px rgb(79,77,73), -23px 23px rgb(223,223,223), -23px -23px rgb(79,77,73), 23px -23px rgb(223,223,223);
		-moz-box-shadow: 23px 23px rgb(79,77,73), -23px 23px rgb(223,223,223), -23px -23px rgb(79,77,73), 23px -23px rgb(223,223,223);
	animation: cssload-spin ease infinite 4.6s;
		-o-animation: cssload-spin ease infinite 4.6s;
		-ms-animation: cssload-spin ease infinite 4.6s;
		-webkit-animation: cssload-spin ease infinite 4.6s;
		-moz-animation: cssload-spin ease infinite 4.6s;
}



@keyframes cssload-spin {
	0%,
	100% {
		box-shadow: 23px 23px rgb(79,77,73), -23px 23px rgb(223,223,223), -23px -23px rgb(79,77,73), 23px -23px rgb(223,223,223);
	}
	25% {
		box-shadow: -23px 23px rgb(223,223,223), -23px -23px rgb(79,77,73), 23px -23px rgb(223,223,223), 23px 23px rgb(79,77,73);
	}
	50% {
		box-shadow: -23px -23px rgb(79,77,73), 23px -23px rgb(223,223,223), 23px 23px rgb(79,77,73), -23px 23px rgb(223,223,223);
	}
	75% {
		box-shadow: 23px -23px #dfdfdf, 23px 23px #4f4d49, -23px 23px #dfdfdf, -23px -23px #4f4d49;
	}
}

@-webkit-keyframes cssload-spin {
	0%,
	100% {
		box-shadow: 23px 23px rgb(79,77,73), -23px 23px rgb(223,223,223), -23px -23px rgb(79,77,73), 23px -23px rgb(223,223,223);
	}
	25% {
		box-shadow: -23px 23px rgb(223,223,223), -23px -23px rgb(79,77,73), 23px -23px rgb(223,223,223), 23px 23px rgb(79,77,73);
	}
	50% {
		box-shadow: -23px -23px rgb(79,77,73), 23px -23px rgb(223,223,223), 23px 23px rgb(79,77,73), -23px 23px rgb(223,223,223);
	}
	75% {
		box-shadow: 23px -23px #dfdfdf, 23px 23px #4f4d49, -23px 23px #dfdfdf, -23px -23px #4f4d49;
	}
}
